// /schemas/sections/logoList.ts
import { defineType, defineField } from 'sanity'
import { ImagesIcon } from '@sanity/icons'

export const logoList = defineType({
    name: 'logoList',
    title: 'Logo List',
    type: 'object',
    icon: ImagesIcon,
    fields: [
        defineField({
            name: 'title',
            title: 'Section Title',
            type: 'string',
        }),
        defineField({
            name: 'itemsPerRow',
            title: 'Items per Row',
            type: 'number',
            initialValue: 3,
            validation: (Rule) => Rule.min(1).max(8),
        }),
        defineField({
            name: 'backgroundColor',
            title: 'Background Color',
            type: 'string',
            options: {
                list: [
                    { title: 'White', value: 'bg-white' },
                    { title: 'Gray 300', value: 'bg-gray-300' },
                    { title: 'Gray 800', value: 'bg-gray-800' },
                    { title: 'Black', value: 'bg-black' },
                    { title: 'Blue 500', value: 'bg-blue-500' },
                    { title: 'Red 500', value: 'bg-red-500' },
                    { title: 'Transparent', value: 'bg-transparent' },
                ],
                layout: 'dropdown', // or 'radio'
            },
        }),
        defineField({
            name: 'logos',
            title: 'Logos',
            type: 'array',
            of: [
                defineField({
                    name: 'logo',
                    type: 'image',
                    options: { hotspot: true },
                    fields: [
                        {
                            name: 'alt',
                            type: 'string',
                            title: 'Alt text',
                        },
                    ],
                }),
            ],
        }),
    ],
    preview: {
        select: {
            title: 'title',
        },
        prepare({ title }) {
            return {
                title: title || 'Logo List Section',
                subtitle: 'Logos',
            }
        },
    },
})
