import { defineType, defineField, defineArrayMember } from 'sanity'
import { StarIcon } from '@sanity/icons'

export const testimonialSlider = defineType({
    name: 'testimonialSlider',
    title: 'Testimonial Slider',
    type: 'object',
    icon: StarIcon,
    fields: [
        defineField({
            name: 'title',
            title: 'Slider Title',
            type: 'string',
        }),
        defineField({
            name: 'testimonials',
            title: 'Testimonials',
            type: 'array',
            of: [
                defineArrayMember({
                    type: 'object',
                    name: 'testimonial',
                    fields: [
                        defineField({ name: 'quote', type: 'text', title: 'Quote' }),
                        defineField({ name: 'author', type: 'string', title: 'Author Name' }),
                        defineField({ name: 'role', type: 'string', title: 'Author Role' }),
                        defineField({ name: 'image', type: 'image', title: 'Author Image' }),
                        defineField({
                            name: 'backgroundColor',
                            title: 'Background Color',
                            type: 'string',
                            options: {
                                list: [
                                    { title: 'White', value: 'bg-white' },
                                    { title: 'Gray 100', value: 'bg-gray-100' },
                                    { title: 'Green 100', value: 'bg-green-100' },
                                    { title: 'Black', value: 'bg-black' },
                                    { title: 'Blue 100', value: 'bg-blue-100' },
                                    { title: 'Red 100', value: 'bg-red-100' },
                                    { title: 'Transparent', value: 'bg-transparent' },
                                ],
                                layout: 'dropdown', // or 'radio'
                            },
                        }),                    ],
                }),
            ],
        }),
    ],
    preview: {
        select: {
            title: 'title',
        },
        prepare({ title }) {
            return {
                title: title || 'Testimonial Slider',
                subtitle: 'Component',
            }
        },
    },
})
