import { defineField, defineType } from "sanity";

export const featuresType = defineType({
    name: "features",
    type: "object",
    fields: [
        defineField({
            name: "title",
            type: "string",
        }),
        defineField({
            name: "features",
            type: "array",
            of: [
                defineField({
                    name: "feature",
                    type: "object",
                    fields: [
                        defineField({
                            name: "title",
                            type: "string",
                        }),
                        defineField({
                            name: "text",
                            type: "string",
                        }),
                        defineField({
                            name: "icon",
                            type: "image",
                        }),
                        defineField({
                            name: 'backgroundColor',
                            title: 'Background Color',
                            type: 'string',
                            options: {
                                list: [
                                    { title: 'White', value: 'bg-white' },
                                    { title: 'Gray 100', value: 'bg-gray-100' },
                                    { title: 'Green 100', value: 'bg-green-100' },
                                    { title: 'Yellow', value: 'bg-yellow' },
                                    { title: 'Blue 100', value: 'bg-blue-100' },
                                    { title: 'Red 100', value: 'bg-red-100' },
                                    { title: 'Transparent', value: 'bg-transparent' },
                                ],
                                layout: 'dropdown', // or 'radio'
                            },
                        }),
                    ],
                }),
            ],
        }),
    ],
    preview: {
        select: {
            title: "title",
        },
        prepare({ title }) {
            return {
                title,
                subtitle: "Features",
            };
        },
    },

});