import { defineField, defineType } from "sanity";

export const cardSectionType = defineType({
    name: "cardSection",
    type: "object",
    fields: [
        defineField({
            name: "title",
            type: "string",
        }),
        defineField({
            name: "cards",
            type: "array",
            of: [
                defineField({
                    name: "card",
                    type: "object",
                    fields: [
                        defineField({
                            name: "title",
                            type: "string",
                        }),
                        defineField({
                            name: "text",
                            type: "string",
                        }),
                        defineField({
                            name: "icon",
                            type: "image",
                            title: "Image",
                        }),
                        defineField({
                            name: "bgimage",
                            type: "image",
                            title: "BG Image",
                        }),
                        defineField({
                            name: "btntext",
                            type: "string",
                            title: "Button Text",
                        }),
                        defineField({
                            name: "btnurl",
                            type: "url",
                            title: "Button URL",
                        }),
                        defineField({
                            name: 'textColor',
                            title: 'Text Color',
                            type: 'string',
                            options: {
                                list: [
                                    { title: 'White', value: 'text-white' },
                                    { title: 'Gray 100', value: 'text-gray-100' },
                                    { title: 'Gray 800', value: 'text-gray-800' },
                                    { title: 'Black', value: 'text-black' },
                                    { title: 'Blue 500', value: 'text-blue-500' },
                                    { title: 'Red 500', value: 'text-red-500' },
                                    { title: 'Transparent', value: 'text-transparent' },
                                ],
                                layout: 'dropdown', // or 'radio'
                            },
                        }),
                    ],
                }),
            ],
        }),
    ],
    preview: {
        select: {
            title: "title",
        },
        prepare({ title }) {
            return {
                title,
                subtitle: "Card Section",
            };
        },
    },

});