import { defineField, defineType } from 'sanity'

export const tabSection = defineType({
    name: 'tabSection',
    title: 'Tab Section',
    type: 'object',
    fields: [
        defineField({
            name: 'tabs',
            title: 'Tabs',
            type: 'array',
            of: [
                defineType({
                    name: 'tab',
                    title: 'Tab',
                    type: 'object',
                    fields: [
                        defineField({
                            name: 'title',
                            title: 'Tab Title',
                            type: 'string',
                        }),
                        defineField({
                            name: 'image',
                            title: 'Tab Image',
                            type: 'image',
                        }),
                    ],
                }),
            ],
        }),
    ],
})
